<template>
  <v-container>
    <div>
      <v-toolbar>
        <v-toolbar-side-icon>
          <v-icon @click="showRanking">menu</v-icon>
        </v-toolbar-side-icon>
        <v-toolbar-title>골드러쉬</v-toolbar-title>
      </v-toolbar>

      <v-container class="ma-0 pa-0" grid-list-xs text-xs-center>
        <v-card mx-3>
          <v-card-text class="text-xs-right">
            <span class="headline">SCORE&nbsp;</span>
            <span class="headline red--text">{{ score }}</span>
          </v-card-text>
        </v-card>
        <!-- v-for index는 1부터 시작, end inclusive -->
        <v-layout v-for="ridx in 9" :key="ridx" row wrap align-center justify-center>
          <!-- row는 역순 배열, row 내 column은 정순 배열 -->
          <v-flex v-for="cidx in columns(ridx)" :key="cidx" md1 grow>
            <v-card v-if="type(cidx) === 1" dark color="white">
              <v-responsive>
                <v-img v-if="status(cidx) === 1" :src="require('@/assets/minimg/correct.png')"/>
                <v-img
                  v-else-if="status(cidx) === -1"
                  :src="require('@/assets/minimg/incorrect.png')"
                />
                <v-img
                  v-else-if="clickable(cidx) === 0"
                  :src="require('@/assets/minimg/block.png')"
                />
                <v-img
                  v-else-if="clickable(cidx) === -1 && !success"
                  :src="require('@/assets/minimg/left.png')"
                  @mousedown="click"
                  @touchstart="click"
                  @click="guess(cidx)"
                  class="blink-image"
                />
                <v-img
                  v-else-if="clickable(cidx) === 1 && !success"
                  :src="require('@/assets/minimg/right.png')"
                  @mousedown="click"
                  @touchstart="click"
                  @click="guess(cidx)"
                  class="blink-image"
                />
                <v-img
                  v-else-if="clickable(cidx) === 7 && !success"
                  :src="require('@/assets/minimg/up.png')"
                  @mousedown="click"
                  @touchstart="click"
                  @click="guess(cidx)"
                  class="blink-image"
                />
              </v-responsive>
            </v-card>
            <v-card v-else dark color="secondary">
              <v-responsive>
                <v-img v-if="type(cidx) === 0" :src="require('@/assets/minimg/start.png')"/>
                <v-img v-else-if="type(cidx) === 99" :src="require('@/assets/minimg/goal.png')"/>
                <v-img
                  v-else-if="type(cidx) === -1"
                  :src="require('@/assets/minimg/obstacle01.png')"
                />
                <v-img
                  v-else-if="type(cidx) === -2"
                  :src="require('@/assets/minimg/obstacle02.png')"
                />
                <v-img
                  v-else-if="type(cidx) === -3"
                  :src="require('@/assets/minimg/obstacle03.png')"
                />
                <v-img v-else :src="require('@/assets/minimg/obstacle04.png')"/>
              </v-responsive>
            </v-card>
          </v-flex>
        </v-layout>
        <!-- 팝업 -->
        <!-- v-if를 안 써주면 팝업이 보이지 않는 DOM 공간을 차지하는 문제가 있다. -->
        <!-- v-if를 써주면 팝업이 조금 늦게 뜨는 느낌이 있는 문제가 있다. -->
        <Word
          v-if="visible"
          :wordDialog.sync="visible"
          :qidx="qidx"
          :word="word"
          :ans1="ans1"
          :ans2="ans2"
          :ans3="ans3"
          :ans4="ans4"
          :answ="answ"
        />
        <Done v-model="show" :show.sync="show" :type="result"/>
      </v-container>

      <v-layout justify-center>
        <v-flex grey lighten-2 py-3 text-xs-center xs12>
          &copy;2019 —
          <strong>Hoopee</strong>
        </v-flex>
      </v-layout>
    </div>

    <Ranking v-model="rank" :rank.sync="rank"/>
  </v-container>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "Game",
  data() {
    return {
      audio: null,
      question: {
        id: 0,
        level: 0,
        word: "",
        a1: "",
        a2: "",
        a3: "",
        a4: "",
        ans: 0
      },
      blocks: [],
      visible: false,
      show: false,
      rank: false,
      result: 0,
      // Word.vue props
      qidx: Number,
      word: String,
      ans1: String,
      ans2: String,
      ans3: String,
      ans4: String,
      answ: String
    };
  },
  components: {
    Word: () => import("./Word.vue"),
    Done: () => import("./Done.vue"),
    Ranking: () => import("./Ranking.vue")
  },
  created() {
    if (this.$store.getters.sound) {
      let sound = require("@/assets/click.wav");
      if (sound) this.audio = new Audio(sound);
    }

    this.getStates();
    this.generateMap();
  },
  beforeDestroy() {
    this.$root.$off("answered");
  },
  destroyed() {
    console.log("game will be destroyed");
  },
  mounted() {
    this.$store.watch(() => this.$store.state.map, () => {}, {
      deep: true
    });
    // 문제 팝업에서 선택 결과를 이벤트로 받아서 state를 업데이트하는 이벤트 핸들러
    this.$root.$on("answered", result => {
      console.log("index in game ", result.index);
      // 응답 결과 반영
      let params = {};
      params.index = result.index;
      if (result.correct) {
        this.$store.commit("setScore", 5); // -1레벨은 5, 자기레벨은 8, +1레벨은 10
        params.status = 1;
      } else {
        params.status = -1;
        this.$store.commit("incrementWrong");
      }
      this.$store.commit("changeStatus", params);
      this.checkdone();
    });
    this.$root.$on("tryagain", () => {
      let rand = Math.floor(Math.random() * 10000) + 1;
      this.$router.replace({ name: "game", params: { rand } });
    });
    this.$root.$on("quit", function() {
      this.$router.go(-1);
    });
  },
  computed: {
    ...mapGetters(["status", "type", "clickable", "movable", "score"]),
    success: function() {
      return (
        this.$store.state.map[55].status == 1 ||
        this.$store.state.map[61].status == 1
      );
    }
  },
  methods: {
    generateMap: function() {
      this.generateBlocks(4);
      this.fillMap(63);
    },
    generateBlocks: function(numBlocks) {
      this.blocks = [];

      let count = 0;
      while (count < numBlocks) {
        // 1~47 사이에 4개의 장애물 위치를 랜덤하게 선택 (0은 시작점, 48은 종료점)
        let newBlock = Math.floor(Math.random() * (61 - 1 + 1)) + 1;
        // 블록이 같은 지점에 겹치는 것을 방지하는 조건
        if (this.blocks.includes(newBlock)) continue;
        // 시작점과 종료점을 모두 막아버리는 경우를 방지하는 조건
        else if (newBlock == 1 && this.blocks.includes(7)) continue;
        else if (newBlock == 7 && this.blocks.includes(1)) continue;
        else if (newBlock == 2 && this.blocks.includes(8)) continue;
        else if (newBlock == 8 && this.blocks.includes(2)) continue;
        else if (newBlock == 61 && this.blocks.includes(55)) continue;
        else if (newBlock == 55 && this.blocks.includes(61)) continue;
        else if (newBlock == 54 || newBlock == 8) continue;
        else {
          this.blocks.push(newBlock);
          count++;
        }
      }
    },
    fillMap: function(size) {
      this.$store.commit("initMap");
      let block = -1;
      for (let i = 1; i < size - 1; i++) {
        let type = this.blocks.includes(i) ? block-- : 1;
        // index 1과 7은 출발점에서의 클릭 가능한 위치(단, 장애 블록이 아니고 정상 블록인 경우)
        let clickable = (i == 1 || i == 7) && type == 1 ? i : 0;
        this.$store.state.map.push({
          type: type,
          status: type > 0 ? 0 : -99,
          // 장애 블록의 status를 0으로 두면 store의 changeStatus에서
          // status가 1(즉, 맞춘경우)일때 장애블록의 clickable도 1로 세팅해 버리는데, 다음 status가 또 1이면 모든 블록의 리셋 과정이 있기 때문에 문제가 없지만
          // status가 -1(즉, 틀린경우)이면 처리되는 내용이 없기 때문에 장애블록의 clickable상태를 지워주지 않게된다.
          // 그 결과로 종료 판별에 문제가 생긴다.
          clickable: clickable
        });
      }
      this.$store.state.map.push({ type: 99, status: 0, clickable: 0 });

      this.$store.state.map.forEach((item, i) =>
        console.log(i, ": ", item.type, ", ", item.status, ", ", item.clickable)
      );
    },
    columns: function(ridx) {
      return this.range((9 - ridx) * 7, (9 - ridx) * 7 + 6);
    },
    range: function(start, end) {
      // end inclusive
      return new Array(end - start + 1)
        .fill(undefined)
        .map((_, i) => i + start);
    },
    checkdone: function() {
      if (this.success) {
        this.$router.replace({ name: "bonus" });
      } else if (
        this.$store.getters.movable == 0 ||
        this.$store.getters.wrong === 3
      ) {
        this.show = true;
      }
    },
    click: function() {
      this.audio.play();
    },
    guess: function(qidx) {
      let data = new FormData();
      data.append("ac_idx", this.$store.getters.ac_idx);
      data.append("level", this.$store.getters.level);

      fetch("https://www.hoopeeworld.co.kr/goldrush/getWords.php", {
        method: "POST",
        body: data
      })
        .then(res => res.json())
        .then(data => {
          this.qidx = qidx;
          this.word = data.english;
          this.ans1 = this.parseAnswer(data.answer1);
          this.ans2 = this.parseAnswer(data.answer2);
          this.ans3 = this.parseAnswer(data.answer3);
          this.ans4 = this.parseAnswer(data.answer4);
          this.answ = parseInt(data.c_answer);
          this.visible = true;
        });
    },
    parseAnswer: function(ans) {
      let temp = "";
      let t1 = ans.indexOf("###");
      let t2 = ans.indexOf("2.");

      if (t1 > t2 && t2 > 0) t1 = t2;
      if (t1 > 0) temp = ans.substring(0, t1);
      else temp = ans;
      return temp
        .replace("1.", "")
        .replace("\n", ",")
        .replace(",,", ",");
    },
    showRanking() {
      this.rank = true;
    }
  }
};
</script>

<style scoped>
html {
  overflow: hidden;
  width: 100%;
}
body {
  overflow: hidden;
  width: 100%;
}
/* Old Firefox */
@-moz-keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
/* IE */
@-ms-keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
/* Opera and prob css3 final iteration */
@keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
.blink-image {
  -moz-animation: blink normal 2s infinite ease-in-out; /* Firefox */
  -webkit-animation: blink normal 2s infinite ease-in-out; /* Webkit */
  -ms-animation: blink normal 2s infinite ease-in-out; /* IE */
  animation: blink normal 2s infinite ease-in-out; /* Opera and prob css3 final iteration */
}
</style>
